import { Component, OnInit } from '@angular/core';
import { ThemeService } from '../../../../../core/components/theme';
import { SkeletonLoadingServiceService } from '../../../../../core/components/layout/services/skeleton-loading-service.service';
import { ChartDataSets, ChartOptions, ChartType } from 'chart.js';
import { Label } from 'ng2-charts';

@Component({
  selector: 'doc-skc-basics-container',
  templateUrl: './basics-container.component.html',
  styleUrls: ['./basics-container.component.less']
})
export class BasicsContainerComponent implements OnInit {
  img = '../../../../../../assets/favicon.ico';
  breadCrumbs = {
    links: ['Home', 'Another pace to Go 1', 'Another pace to Go 2'],
    separator: '/'
  };
  loadingSkeleton = false;

  constructor(
    private themeService: ThemeService,
    private skeletonLoadingServiceService: SkeletonLoadingServiceService
  ) {}

  ngOnInit(): void {
    this.onSkeletonToggle();
  }

  toggle() {
    const active = this.themeService.getActiveTheme();

    if (active.name === 'second') {
      this.themeService.setTheme('main');
    } else {
      this.themeService.setTheme('second');
    }
  }

  private onSkeletonToggle() {
    this.skeletonLoadingServiceService.skeletonChange.subscribe(value => (this.loadingSkeleton = value));
  }
}
